
// eslint-disable-next-line import/no-mutable-exports
export let isWinFocus = true;
window.addEventListener('focus', () => {
  isWinFocus = true;
}, false);
window.addEventListener('blur', () => {
  isWinFocus = false;
}, false);
export function focusWatch(handler: (isFocus: boolean) => void) {
  const focus = () => {
    handler(true);
  };
  const blur = () => {
    handler(false);
  };
  window.addEventListener('focus', focus, false);
  window.addEventListener('blur', blur, false);
  return () => {
    window.removeEventListener('focus', focus, false);
    window.removeEventListener('blur', blur, false);
  };
}
export function untilFocus() {
  return new Promise<void>(resolve => {
    const onFocus = () => {
      resolve(undefined);
      window.removeEventListener('focus', onFocus, false);
    };
    window.addEventListener('focus', onFocus, false);
  });
}
export default focusWatch;
