


































































































































































import {
  defineComponent, reactive, computed, ref, toRefs, watch, onMounted, getCurrentInstance,
} from '@vue/composition-api';
import { getContextUser } from '@/loaders/context';
import BaseImage from '@/components/base/Image';
import setLang, {
  languageNumber2lang,
  lang2TranslatedLang,
  langMapLangNum,
  langConfigList,
} from '@/router/utils/set-lang';
import i18n from 'i18next';
import axios from '@/utils/axios';
import isQt from '@/utils/is-qt';
import openWindow from '@/utils/open-window';
import exec from '@/utils/exec';
import Paging from '@/models/Paging';
import User from '@/models/User';
import { Meeting } from './typings';
import OnlineUsers from './components/OnlineUsers.vue';
import useInMeeting, { onlineMeetingUsers } from './utils/useInMeeting';

const Header = defineComponent({
  name: 'meet__header',
  components: { BaseImage, OnlineUsers },
  props: {
    meeting: {
      type: Object as unknown as () => Meeting,
      required: true,
    },
  },
  setup(props, { emit, root }) {
    const state = reactive({
      tmpLang: 0,
      oldLang: 0,
      drawer: false,
      // meetUsers: [] as User[],
      showMeetingUsersDrawer: false,
    });
    const { excludeRouteNames } = useInMeeting({ meeting: props.meeting });
    const isShowRoom = computed(() => props.meeting?.type === 2);
    const isShowLogo = computed(() => props.meeting.logo);
    const isShowLibary = computed(() => !isShowRoom.value && !['UpMeetingLibaray'].includes(root.$route.name || ''));
    const user = computed(() => getContextUser());
    const userImage = computed(() => user.value?.headImg || require('@/assets/img/header/default_avatar.png'));
    const userTitle = computed(() => user.value?.name || user.value?.account || '');


    onMounted(() => {
      state.tmpLang = langMapLangNum(i18n.language).toString();
      state.oldLang = state.tmpLang; // 用于取消确认时回退
    });

    const changeType = (operateType: 'edit' | 'review') => {
      root.$router.push({
        name: 'upMeetingHome', query: { meetingId: props.meeting.id }, params: { operateType },
      });
    };

    const popInMeetingUsers = () => {
      state.showMeetingUsersDrawer = false;
      state.drawer = !state.drawer;
    };


    const toMeetStore = async () => {
      const id = props.meeting.entryId;
      const entryAppType = '26';
      const meetingId = props.meeting.id;
      if (isQt()) {
        exec('openMeetingLibrary', { entryAppType, id, meetingId });
      } else {
        openWindow({
          path: '/up-meeting/library',
          query: {
            id,
            entryAppType,
            meetingId,
          },
        });
      }
    };

    const handleSetLang = async (langNum: any) => {
      const lang: any = languageNumber2lang(langNum);
      try {
        await root.$confirm(i18n.t('setting.confirm_change_language', {
          lang: i18n.t(`setting.${lang2TranslatedLang(lang)}`),
        }), i18n.t('setting.confirm'));


        setLang(lang);
      } catch (e) {
        state.tmpLang = state.oldLang;
      }
    };

    // 获取会议成员列表
    // const fetchMeetUsers = async (pageNumber = 1) => {
    //   const pageSize = 10000;
    //   const res = (await axios({
    //     url: '/meeting/member/page',
    //     params: { cooperationId: props.meeting.cooperationId, pageNumber, pageSize },
    //   }))?.data as Paging<{ userId: string }>;
    //   const userIds = res.records!.map(i => i.userId);
    //   const users = (await axios({
    //     method: 'post',
    //     url: 'authcenter/user/im/info',
    //     data: userIds,
    //   })).data as User[];
    //   return userIds.map(id => users.find(u => u.id === id)!);
    // };


    // const init = async () => {
    //   state.meetUsers = await fetchMeetUsers();
    // };

    // init();


    return {
      ...toRefs(state),
      toMeetStore,
      userImage,
      userTitle,
      langConfigList,
      handleSetLang,
      changeType,
      isShowRoom,
      isShowLogo,
      isShowLibary,
      popInMeetingUsers,
      onlineMeetingUsers,
      excludeRouteNames,
    };
  },
});

export default Header;
