import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext, onUnmounted,
} from '@vue/composition-api';
import focusWatch from '@/utils/focus-watch';
import axios from '@/utils/axios';
import User from '@/models/User';
import im from '@/get-im';
import tryParseJson from '_/try-parse-json';
import SdSocket from '@/utils/sd-socket';
import { has } from '@/utils/own';
import delay from '@4dst-saas/public-utils/dist/delay';
import { Meeting } from '../typings';

interface Message {
  type: number,
  data: {
    userId: string,
    zoneId: string,
    status: 1|2
  }
}
interface Options {
  excludeRouteNames?: string[],
  meeting: Meeting
}

export type Users = User[];

export const isCreator = ({ meeting, user }:{meeting:Meeting, user:User}) => {
  return meeting.creator === user.id;
};

const fetchInMeetingUsers = async (params:{zoneId:string}) => {
  const userIds:string[] = (await axios({
    url: '/im/online/users',
    params,
  })).data;
  if (!userIds?.length) return [];
  const users = (await axios({
    method: 'post',
    url: 'authcenter/user/im/info',
    data: userIds,
  })).data as Users;
  return users;
};

const updateOnlineStatus = async ({ meetingId, status }:{meetingId:string, status:1|2}) => {
  await axios({
    url: '/im/online/change',
    method: 'post',
    data: {
      zoneId: meetingId,
      status,
    },
  });
};


// 全局状态,后续其它地方可以直接useInMeeting直接获取
export const onlineMeetingUsers = ref<Users>([]);

const useInMeeting = ({ excludeRouteNames = ['UpMeetingLibaray'], meeting }:Options) => {
  const _this = getCurrentInstance();
  const state = reactive({
    connector: null as SdSocket | null,
    removeFocusEvent: null as Function | null,
  });
  const setMeetingUsers = async () => {
    onlineMeetingUsers.value = await fetchInMeetingUsers({ zoneId: meeting.id });
  };
  const focusChange = async (isFocus:boolean) => {
    if (excludeRouteNames.includes(_this?.$route.name!)) return;
    console.log('window is focus:', isFocus);
    await updateOnlineStatus({
      meetingId: meeting.id, status: isFocus ? 1 : 2,
    });
    // 失焦时不触发ws消息,手动更新meetingUsers(防止focusChange不触发message,可能会多调接口: fix IDEATION-2495)
    if (!isFocus) {
      await setMeetingUsers();
    }
  };

  onMounted(async () => {
    // window焦点监听
    state.removeFocusEvent = focusWatch(focusChange);
    // 初始将自己添加进在线成员,获取在线成员
    await focusChange(true);
    await setMeetingUsers();
    // 监听websocket
    try {
      const socket = SdSocket.getInstance();
      socket.on('message', async (evt, message) => {
        if (has(message, 'type') && message.type === 3) {
          await setMeetingUsers();
        }
      });
      // socket.on('switchConnector', () => {
      //   console.log('重连了');
      //   // 重连再触发一次focus
      //   socket.off('message');
      //   socket.on('message', async (evt, message) => {
      //     if (has(message, 'type') && message.type === 3) {
      //       await setMeetingUsers();
      //     }
      //   });
      //   focusChange(true);
      // });
      state.connector = socket;
    } catch (error) {
      // 不继续抛错
      console.log('useInMeeting:', error);
    }
  });

  onUnmounted(async () => {
    state.connector = null;
    onlineMeetingUsers.value = [];
    await focusChange(false); // 先触发下线
    state.removeFocusEvent?.();
    state.removeFocusEvent = null;
  });

  return {
    ...toRefs(state),
    onlineMeetingUsers,
    focusChange,
    excludeRouteNames,
  };
};

export default useInMeeting;

